import validator from "@rjsf/validator-ajv8";
import { useEffect, useState } from "react";
import Form from "./Form";

const field = new window.SquidexFormField();

const App = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [schema, setSchema] = useState(undefined);
  const [isDisabled, setIsDisabled] = useState(false);
  const [value, setValue] = useState(undefined);

  useEffect(() => {
    field.onDisabled((disabled) => {
      setIsDisabled(disabled);
    });

    field.onValueChanged(function (newValue) {
      setValue(newValue);
    });

    if (window.location.hash && window.location.hash.length > 1) {
      fetch(window.location.hash.replace("#", ""))
        .then((x) => x.json())
        .then((x) => {
          setSchema(x);
          setIsLoading(false);
        });
    }
  }, []);

  const doBlur = () => {
    field.touched();
  };

  const doChange = (editor) => {
    if (editor.errors.length === 0) {
      field.valueChanged(editor.formData);
      setValue(editor.formData);
    }
  };

  return (
    <div className="ama-i18n-editor">
      {isLoading && <h1>Loading...</h1>}

      {!isLoading && !schema && (
        <h1>Schema not found. Check the url of the json schema provided.</h1>
      )}

      {!isLoading && !!schema && (
        <Form
          schema={schema}
          formData={value}
          validator={validator}
          disabled={isDisabled}
          onChange={doChange}
          onBlur={doBlur}
          children={[]}
        />
      )}
    </div>
  );
};

export default App;
